.auth-container {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
}

.social_login-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.btn {
  border-radius: 10px;
  font-size: 16px;
  color: #fff;
  border: 0;
  margin-top: 20px;
  width: 40vw;
}

.google-btn {
  padding: 5px 20px;
  background-color: #cd2222;
}

.google-btn:hover {
  background-color: #f51a1a;
}

.github-btn {
  padding: 5px 20px;
  background-color: #2c313a;
}

.github-btn:hover {
  background-color: #3f4755;
}

@media screen and (max-width: 420px) {
  .btn {
    width: 80vw;
  }
}

h1 {
  color: #fff;
  align-self: center;
}