#profile-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.profile-input {
  width: 30vw;
  font-size: 18px;
  border: 0;
  border-bottom: 2px solid #4FA4F7;
  background-color: #000;
  color: #fff;
}

.btn {
  cursor: pointer;
}

.update-btn {
  width: 30vw;
  background-color: #2679F2;
}

.update-btn:hover {
  background-color: #4FA4F7;
}

.logout {
  display: flex;
  justify-content: center;
}

.logout-btn {
  width: 30vw;
  margin-top: 20px;
  background-color: #da2222;
  align-self: center;
}

.logout-btn:hover {
  background-color: #f23737;
}

@media screen and (max-width: 420px) {
  .logout-btn,
  .profile-input,
  .update-btn {
    width: 70vw;
  }
}